import uniIcon from "@/components/uni-icons/uni-icons.vue"
export default {
	components: {
		uniIcon
	},
	onLoad(e) {
		if (e.num == 1) {
			uni.setNavigationBarTitle({
				title: '编辑收货地址'
			})
		} else {
			uni.setNavigationBarTitle({
				title: '新增收货地址'
			})
		}
	},
	methods: {
		toChooseAddress() {
			uni.navigateTo({
				url:'../chooseAddress/chooseAddress'
			})
		}
	}
}
